
html, body { 
  margin:0; 
  padding:0; 
  height:100%; 
}

h1 {
  margin: 0;
}

a {
  color: blue;
}

#editor {
  width: 600px !important;
  height: 775px !important;
  margin-right: 20px;
  display: inline-block;
}

iframe {
  border: 1px solid black;
}

#mainDiv {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #333333;
  flex: 1;
  position: absolute;
  height: 100%;
  width: 100%;
  color: white;
  /* border:  1px solid green; */
}

#headerDiv {
  display: flex;
  flex-direction: row;
  border:  2px solid blue;
  color: white;
  background-color: slategrey;
  }

#imageDiv {
  display: flex;  
  flex: 1;
  padding-top: 0px;
}

#titleDiv {
  display: flex;
  flex-direction: column;
  flex: 3;
}

#sewerReportDiv {
  display: flex;
  flex-direction: row;
  flex: 1;
  height: 100vh; 
  overflow: hidden;
}

#sewerReportInputDivContainer {
  height: 100vh; 
  overflow-y: scroll;
}

#sewerReportInputDiv {
  display: flex;
  flex-direction: column;
  align-items: left;
  /* flex: 3; */
  padding-left: 20px;
  padding-right: 20px;
  overflow: hidden;
  overflow-y: scroll;
  height: 100vh; 
}

#sewerReportPreviewDiv {
  margin-left: 20px;
  overflow: hidden;
  /* overflow-y: scroll; */
  height: 100vh; 
}

.alignedLabels {
  display: inline-block;
  width: 140px;
  text-align: right;
  margin-right: 10px;
}

#totalReplacementCostLabel {
  display: inline-block;
  width: 240px;
  text-align: right;
  margin-right: 10px;
}

.footerButton {
  margin-top: 20px;
  margin-bottom: 20px;
  align-self: center;
  width: 150px;
  height: 40px;
  background-color: blue;
  color: white;
  min-height: 40px;
  margin: 20px;
}

#buttonDiv {
  display: flex;
  flex-direction: row;
  align-self: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

#videoEntryDiv {
  display: inline-block;
}

.videoButtons {
  width: 30px;
  height: 23px;
  text-align: center;
}